h1 {
  text-align: center;
}
.select {
  overflow: hidden;
  text-align: center;
  margin: 0 auto;
  max-width: 1000px;
}

.select img {
  width: 80px;
  margin: 20px;
  padding: 3px;
  display: inline-block;
}

.select img:hover {
  cursor: pointer;
}

.wins span {
  color: green;
}

.losses span {
  color: red;
}

.draws span {
  color: grey;
}
.panel-left {
  width: 40vw;
  display: inline-block;
  margin: 0 5px;
  text-align: center;
}

.panel-left span img {
  width: 50px;
}

.panel-right {
  width: 40vw;
  display: inline-block;
  margin: 0 5px;
  text-align: center;
}
.red {
  color: red;
}
.green {
  color: green;
}
.grey {
  color: grey;
}
