#go-top p {
  font-size: 12px;
  transition-duration: 0.5s;
}

#go-top:hover p {
  color: #494949;
  transform: translateY(-10px);
  transition-duration: 0.5s;
}

#go-top {
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
#go-top {
  font-size: 0.5rem;
  text-align: center;
  margin-left: 50%;
  transform: translateX(-50%);

  border: 1px solid #fff;

  padding: 10px;
  width: 25px;
  line-height: 2px;
  border-radius: 150%;
  transition-duration: 0.4s;
}
#go-top:hover {
  background-color: #fff;
  color: #494949;
  cursor: pointer;
  border: 1px solid #fff;
  transition-duration: 0.4s;
}

footer div {
  display: block;
  margin: 0 auto;
  width: 80%;
}

footer {
  background-color: #494949;
  color: rgba(255, 255, 255, 0.7);
  padding: 5px 10px;
}
footer h2 {
  text-transform: uppercase;
  letter-spacing: 1px;
  color: #5dc3dc;
  font-size: 2rem;
}
footer p {
  width: 90%;
  font-size: 1.5rem;
  margin: 0;
  color: #fff;
  padding: 1rem 0;
}

footer a {
  text-decoration: none;
  letter-spacing: 1px;
  color: #5dc3dc;
  padding-bottom: 4px;
  margin: 10px;
}
footer a:hover {
  color: #fff;
}
