.reposContainer {
  h1 {
    font-size: 26px;
    color: #494949;
    text-transform: uppercase;
    letter-spacing: 3px;
    border-bottom: 1px solid #c4c4c4;
    padding-bottom: 15px;
  }

  button {
    border: 1px solid #494949;
    color: #494949;
    font-size: 16px;
    letter-spacing: 2px;
    font-weight: 400;
    border-radius: 2px;
    &:hover {
      color: #fff;
      background-color: #494949;
    }
  }
  .reposList {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    .single-repo {
      position: relative;
      display: flex;
      flex-direction: column;
      padding: 15px 0;
      margin: 15px 10px;
      text-align: center;
      background-image: url(../img/5564.jpg);
      background-size: cover;
      background-position: top;
      background-repeat: no-repeat;
      min-height: 300px;
      flex-basis: 30%;
      border-radius: 30px;
      box-shadow: -15px 15px 12px 2px #c4c4c4;
      @media (max-width: 769px) {
        flex-basis: 100%;
      }

      .github-logo {
        text-align: left;
        margin: 15px;
      }
      h4 {
        text-transform: uppercase;
        letter-spacing: 2px;
        font-size: 14px;
        display: block;
        margin: 0 auto;
        color: #fff;
        border-bottom: 1px solid #fff;
        padding-bottom: 20px;
        @media (max-width: 535px) {
          position: relative;
          display: block;
          margin: 0 auto;
          right: auto;
        }
      }

      h6 {
        font-size: 0.8rem;
        color: #5dc3dc;
        text-align: left;
        margin-top: 20px;
        margin-left: 15px;
        margin-bottom: 0px;
        font-size: 1.2rem;
      }
      p {
        color: #fff;

        font-size: 0.8rem;
        display: inline-block;

        @media (max-width: 535px) {
          display: none;
        }

        &:last-child {
          position: absolute;
          bottom: 10px;
          font-size: 0.8rem;
          border-radius: 2px;
          width: 100%;
          background-color: #5dc3dc;
          text-align: center;
          display: flex;
          justify-content: space-around;
          a {
            color: #fff;
            &:hover {
              color: #f59300;
            }
          }

          span {
            margin: 0 5px;
            display: inline-block;
            letter-spacing: 4px;
            line-height: 1.6rem;
          }
        }
      }
    }
  }
}
