.titleImg img {
  height: 250px;
  max-width: 90vw;
  padding: 0;
}
.article {
  border-bottom: 1px dashed black;
  padding-bottom: 20px;
  text-align: center;
}

.article_tag {
  font-style: italic;
  font-size: 0.8em;
}

.article_desc {
  font-size: 1.5em;
  text-align: justify;
}

.article_cont {
  font-size: 1.2em;
  text-align: justify;
}

a {
  text-decoration: none;
  color: #4b4b4b;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition-duration: 0.3s;
}
a:hover {
  color: #f59300;
  transition-duration: 0.3s;
}
