html {
  scroll-behavior: smooth;
  padding: 0;
  margin: 0;
}
body {
  margin: 0;
  padding: 0;
  font-size: 12px;
  color: #494949;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  word-wrap: break-word;
}
h1 {
  font-size: 26px;
  color: #494949;
  text-transform: uppercase;
  letter-spacing: 3px;
  border-bottom: 1px solid #c4c4c4;
  padding-bottom: 15px;
}

.page {
  padding: 30px 0;
}
button {
  display: block;
  padding: 5px 20px;
  border: 2px solid #494949;
  text-transform: uppercase;
  background-color: #fff;
  color: #494949;
  font-weight: bold;
  margin: 10px auto;
  transition: 0.3s;
  font-size: 30px;
  cursor: pointer;
}

button:hover {
  background-color: #494949;
  color: #fff;
}
