section {
  margin: 0 auto;
  padding: 0 40px;
  width: 85%;
  border-bottom: 1px solid #c4c4c4;
  h2 {
    text-transform: uppercase;
    letter-spacing: 2px;
    color: #494949;
    font-size: 2rem;
  }
}

.courses-container {
  display: flex;
  flex-direction: column;

  margin: 60px 0px;
}
.singleCourse,
.conteiner-edu,
.conteiner-exp {
  display: flex;
  flex-direction: row;
  margin: 15px 0;
  @media (max-width: 769px) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
@media (min-width: 868px) {
  .skills-container {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    margin: 60px 0;
  }
}
@media (max-width: 868px) {
  .skills-container h3 {
    margin-top: 40px;
  }
}

@media (max-width: 400px) {
  button {
    transform: scale(0.6);
  }
  section h2,
  h1 {
    font-size: 1rem;
  }
}
.skills-container h3 {
  text-transform: uppercase;
  letter-spacing: 1px;
}

.skills-container {
  display: flex;
  justify-content: space-between;
  margin: 20px 40px;
  @media (max-width: 769px) {
    flex-direction: column;
  }
}
.skills-container ul {
  list-style-image: url(../img/ulstyle.jpg);
}
.skills-container ul li {
  font-size: 1rem;
  line-height: 2rem;
  letter-spacing: 1px;
}
.course-name,
.exp-name,
.edu-name {
  font-size: 1.1rem;
  word-wrap: break-word;
  padding: 0 10px;
  line-height: 1.6rem;
  width: 25%;
  @media (max-width: 769px) {
    width: 100%;
    text-align: center;
  }
}

.course-description,
.exp-decription,
.edu-decription {
  border-left: 2px solid #5dc3dc;
  padding: 0 10px;
  font-size: 1rem;
  line-height: 1.6rem;
  width: 65%;
  @media (max-width: 769px) {
    border-top: 2px solid #5dc3dc;
    border-left: none;
    width: 100%;
    padding: 10px 0;
    margin: 10px 0;
  }
}

button.showCertificate {
  font-size: 12px;
  padding: 5px 3px;
  color: #494949;
  border: 1px solid #494949;
  &:hover {
    color: #fff;
    background-color: #494949;
  }
}

.certyficate {
  width: 100%;
  padding: 10px 5px;
  img {
    width: 100%;
  }
}
